.aside-active-btn {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 20px;
  cursor: pointer;
  user-select: none;
  margin: 5px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  transition: background-color 0.2s;
}

.aside-active-btn:hover {
  background-color: rgba(0, 0, 0, 0.15);
}

.aside {
  position: fixed;
  z-index: 2;
  right: 0px;
  width: 150px;
  padding: 10px;
  height: 100%;
  margin-right: -180px;
  background-color: #fafafaee;
  box-shadow: -2px 2px 5px rgba(0, 0, 0, 0.2);
  transition: margin-right 0.5s;
}

.aside-enter-active,
.aside-enter-done {
  margin-right: 0px;
}

.aside-exit-active,
.aside-exit-done {
  margin-right: -180px;
}

.aside-item {
  height: 75px;
}

.aside-label {
  font-size: 16px
}

.aside-input-item {
  width: 100%;
  height: 28px;
  padding-left: 5px;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  transition: border 0.2s;
}

.aside-input-item:focus {
  outline: none;
  border-radius: 5px;
  border: 1px solid rgb(126, 158, 255);
  box-sizing: border-box;
}

.aside-option:disabled {
  background-color: #eee;
  color: #36f;
  font-weight: bold;
  font-size: 15px;
}

