.header {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: rgb(210, 232, 255);
  position: relative;
  z-index: 1;
  display: flex;
}

.header-logo {
  height: 40px;
  width: 40px;
  padding: 5px;
}

.header-title {
  display: inline-block;
  line-height: 50px;
  font-size: 20px;
  color: #444;
}